import React, { useState, useEffect } from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import EditIcon from '@mui/icons-material/Edit';
import PeopleIcon from '@mui/icons-material/People';
import { useNavigate } from 'react-router-dom';
import { Delete, DeleteIcon } from 'lucide-react';

function BasicTable() {
  const [data, setData] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('/api/BusinessDetails/'); // featching the data from server
        const result = await response.json();
        console.log(result); // Add this line to check the structure of the result
        if (result.status === 'success') {
          setData(result.data); // Update to use result.data
        } else {
          console.error('API request failed:', result.message);
        }
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    fetchData();
  }, []);

  const handleEditClick = async (businessId) => {
    try {
      const response = await fetch(`/api/BusinessDetails/${businessId}/`);
      const result = await response.json();
      if (result.status === 'success') {
        // Navigate to Farmersdetails component with the fetched data
        navigate(`/Businessdetails/${businessId}`, { state: { formData: result.data } });
      } else {
        console.error('API request failed:', result.message);
      }
    } catch (error) {
      console.error('Error fetching data:', error);
    }
  };

  const handlePeopleClick = (businessId) => {
    navigate(`/Users/${businessId}`);
  };

  const tablestyle={
    background : '#D9D9D9',
  }

  const tablecellstyle={
    border: '1px solid #000',
    background: '#FFF',
  }
  
  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} aria-label="simple table">
        <TableHead style={tablestyle}>
          <TableRow>
            <TableCell align="center">Business ID</TableCell>
            <TableCell align="center">Business Name</TableCell>
            <TableCell align="center">Contact No</TableCell>
            {/* <TableCell align="center">Multiuser</TableCell>
            <TableCell align="center">Status</TableCell> */}
            <TableCell align="center">Action</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {data.map((row) => (
            <TableRow
              key={row.businessid}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell component="th" scope="row" style={tablecellstyle}>
                {row.businessid}
              </TableCell>
              <TableCell align="center" style={tablecellstyle}>{row.businessname}</TableCell>
              <TableCell align="center" style={tablecellstyle}>{row.contactno}</TableCell>
              {/* <TableCell align="center" style={tablecellstyle}>{row.multiuser}</TableCell>
              <TableCell align="center" style={tablecellstyle}>{row.status}</TableCell> */}
              <TableCell align="center" style={tablecellstyle}>
                <button onClick={() => handleEditClick(row.businessid)}>
                  <EditIcon />
                </button>
                &nbsp;
                <button onClick={() => handlePeopleClick(row.businessid)}>
                  <PeopleIcon />
                </button>
                &nbsp;
                <button>
                  <DeleteIcon />
                </button>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}

export default BasicTable;
